@import "https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400..900;1,400..900&display=swap";

@mixin playfair-($uniquifier, $weight) {
  .playfair-#{$uniquifier} {
    font-family: "Playfair Display", serif;
    font-style: normal;
    font-weight: $weight;
    font-optical-sizing: auto;
  }
}

@include playfair-('black', 900);  // Genera una clase con weight 700
@include playfair-('bold', 800);  // Genera una clase con weight 700
@include playfair-('semibold', 700);  // Genera una clase con weight 700
@include playfair-('medium', 600);  // Genera una clase con weight 400
@include playfair-('regular', 500);  // Genera una clase con weight 400
@include playfair-('light', 400);  // Genera una clase con weight 400

// Animacin de entrada y salida de pgina

@keyframes slideInLeft {
  0% {
    transform: translateX(100%); /* Fuera del viewport a la derecha */
    opacity: 1;
  }
  100% {
    transform: translateX(0); /* Dentro del viewport */
    opacity: 1;
  }
}


@keyframes slideOutRight {
  0% {
    transform: translateX(0); /* Dentro del viewport */
    opacity: 1;
  }
  100% {
    transform: translateX(100%); /* Fuera del viewport a la derecha */
    opacity: 1;
  }
}

@keyframes slideOutLeft {
  0% {
    transform: translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateX(-100%);
    opacity: 1;
  }
}

.slide-out-left {
  animation: slideOutLeft 0.5s ease-in forwards;
}


/* Clase para activar la animacin */
.slide-in {
  animation: slideInLeft 0.5s ease-out forwards;
}

.slide-out {
  animation: slideOutRight 0.5s ease-in forwards;
}


